.Wallpaper {
    width: 100%;
    height: 100%;
    background-image: url(http://m.gettywallpapers.com/wp-content/uploads/2022/08/Windows-11-4k-Wallpaper-scaled.jpg);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    background-color: #000000;
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
}